#ctr-login-form{
    background: white;
    padding:10px;
    box-sizing: border-box;
    width:450px;
    height:250px;
    border:0.5px solid rgba(0,0,0,0.1);
    border-radius: 3px;
    box-shadow: 1px 2px 10px rgba(0,0,0,0.2);

    display: flex;
    flex-wrap: wrap;
    justify-content:flex-start;
    align-items: center;
}

#ctr-login-form #title{
    font-weight: 300;
    font-size:30px;
    color:gray;
}

#ctr-login-form #login-form{
    background: white;
    padding:10px;
    box-sizing: border-box;
    width:100%;
    height:70%;
    border:0.5px solid rgba(0,0,0,0.05);
    border-radius: 3px;

    display: flex;
    flex-wrap: wrap;
    justify-content:center;
    align-items: center;
}

#ctr-login-form #login-form .field{
    width:100%;
    height:35px;
    border:1px solid rgba(0,0,0,0.1);
    border-radius:3px;
    padding:0 5px;
    box-sizing: border-box;
    margin:0 0 5px 0;
    color:gray;
    outline-color:purple;
}

#ctr-login-form #login-form .btn{
 height: 30px;
 padding:0 15px;
 width:45%;
 margin:0 5px 0 0;
 border:none;
 border-radius:3px;
 cursor: pointer;
}

#ctr-login-form #login-form #btn-login{
    background: purple;
    color:white;   
    outline:none;
}

#ctr-login-form #login-form #btn-cancel{
    background: white;
    color:purple;
    border:1px solid purple; 
    outline:none;  
}


